/* GENERAL */
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #121212;
}

body,
html {
  overflow-x: hidden;
}

h1,
h2,
p {
  color: #ffffff;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 32px 16px;
  box-sizing: border-box;
}

/* NAV */

nav {
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
  padding: 10px 0;
  height: 64px;
}

.nav__floating {
  position: fixed;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 100%;
  height: auto;
  border-radius: 12px;
  z-index: 100;
  backdrop-filter: blur(6px);
  background: rgba(0, 0, 0, 0.9);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);
  animation: fadeInNav 0.5s ease-in-out;
  transition: opacity 0.4s ease;
}

.nav__centered-header {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.nav__visible {
  opacity: 1;
  pointer-events: auto;
}

.nav__hidden {
  opacity: 0;
  pointer-events: none;
}

@keyframes fadeInNav {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.nav__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1.5rem;
  height: 64px;
}

.nav__link--primary {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background-color: #bb86fc;
  color: #121212;
  border-radius: 24px;
  font-weight: 600;
  text-decoration: none;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 0 10px rgba(187, 134, 252, 0.3);
}

.nav__link--primary:hover {
  background-color: #985eff;
  transform: scale(1.03);
  box-shadow: 0 0 15px rgba(152, 94, 255, 0.5);
}

.nav__links {
  position: absolute;
  left: 44%;
  display: flex;
  align-items: center;
  gap: 2rem;
  list-style: none;
}

.nav__links a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 500;
  transition: color 0.3s ease, transform 0.3s ease;
  position: relative;
}

.nav__links a::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 2px;
  bottom: -4px;
  left: 0;
  background-color: #bb86fc;
  transition: width 0.3s ease;
}

.nav__links a:hover {
  color: #bb86fc;
  transform: scale(1.05);
}

.nav__links a:hover::after {
  width: 100%;
}

.nav__left,
.nav__right {
  flex: 1;
}

.nav__center {
  display: flex;
  justify-content: center;
  list-style: none;
  gap: 2rem;
  flex: 1;
}

.nav__center li a {
  color: #ffffff;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav__center li a:hover {
  color: #bb86fc;
}

.nav__right {
  display: flex;
  justify-content: flex-end;
}

.nav__container {
  width: 100%;
  background-color: #181818;
}

.nav__header {
  font-size: 32px;
  color: #ffffff;
  animation: scaleText 2s infinite ease-in-out;
  user-select: none;
}

@keyframes scaleText {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.15);
  }
}

.logo__img {
  height: 112px;
  width: auto;
  pointer-events: none;
  user-select: none;
}

.logo__img--wrapper {
  display: flex;
  align-items: center;
}

/* HERO */

#hero::before,
#hero::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 9vh;
  background-color: black;
  z-index: 20;
  pointer-events: none;
}

#hero::before {
  top: 0;
}

#hero::after {
  bottom: 0;
}

#hero {
  position: relative;
  height: 100vh;
  background-image: url("./assets/splash-background.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 4rem;
}

.hero__content-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1400px;
  z-index: 5;
  gap: 2rem;
}

.hero__container {
  width: 100%;
  padding: 0 2rem;
}

.hero__content {
  background: rgba(0, 0, 0, 0.5);
  padding: 2rem;
  border-radius: 12px;
  backdrop-filter: blur(6px);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}

.hero__header {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero__subtext {
  font-size: 1.25rem;
  margin-bottom: 2rem;
  color: #ccc;
}

.hero__text {
  z-index: 10;
  color: white;
  max-width: 600px;
  text-align: center;
  margin-right: auto;
  margin-left: 650px;
  padding-left: 2rem;
}

.cta-button {
  display: inline-block;
  padding: 0.75rem 2rem;
  background-color: #bb86fc;
  color: #121212;
  border-radius: 8px;
  font-weight: bold;
  text-decoration: none;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 12px rgba(187, 134, 252, 0.3);
}

.cta-button:hover {
  background-color: #985eff;
  transform: scale(1.05);
}

.nft__wrapper {
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  z-index: 5;
}

.nft__featured {
  width: 100%;
  max-width: 700px;
  filter: drop-shadow(0 0 10px rgba(255, 215, 0, 0.7))
    drop-shadow(0 0 30px rgba(255, 215, 0, 0.5));
}

/* FOOTER */

.footer {
  background-color: #000000;
  color: #b0b0b0;
  padding: 2rem 1rem;
  text-align: center;
  border-top: 1px solid #2c2c2c;
}

.footer__content {
  max-width: 1200px;
  margin: 0 auto;
}

.footer__links {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 1rem 0 0;
}

.footer__links a {
  color: #bb86fc;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer__links a:hover {
  color: #985eff;
}

/* RAIN EFFECT */
.rain-overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 10;
}

.raindrop {
  position: absolute;
  top: -10%;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 1px;
  animation: rain-fall linear infinite;
}

@keyframes rain-fall {
  to {
    transform: translateY(120vh);
  }
}
